export default {
  path: '/university',
  component: () => import('@/views/School'),
  meta: {
    requireAuth: true,
    roles: ['university']
  },
  children: [{
      path: 'demand',
      component: () => import('@/components/DemandList'),
      meta: {
        requireAuth: true,
        roles: ['university']
      },
      children: [{
        path: 'list/:subject',
        components: {
          list: () => import('@/components/DemandList/list.vue'),
        },
        props: {
          list: true
        },
        meta: {
          requireAuth: true,
          roles: ['university']
        },
      }, {
        path: 'list/:subject/detail/:id',
        components: {
          detail: () => import('@/components/DemandList/detail.vue')
        },
        props: {
          detail: true
        },
        meta: {
          requireAuth: true,
          roles: ['university']
        },
      }]
    },
    {
      path: 'realeaseScience',
      component: () => import('@/components/ReleaseScience'),
      meta: {
        requireAuth: true,
        roles: ['university']
      },
    }
  ],
  redirect: '/university/demand/list/机械'
}