export default {
  path: '/company',
  meta: {
    requireAuth: true,
    roles: ['company']
  },
  component: () => import('@/views/Company'),
  children: [{
      path: 'research',
      component: () => import('@/components/ResearchList'),
      meta: {
        requireAuth: true,
        roles: ['company']
      },
      children: [{
        path: 'list/:subject',
        components: {
          list: () => import('@/components/ResearchList/list.vue'),
        },
        props: {
          list: true
        },
        meta: {
          requireAuth: true,
          roles: ['company']
        },
      }, {
        path: 'list/:subject/detail/:id',
        components: {
          detail: () => import('@/components/ResearchList/detail.vue')
        },
        props: {
          detail: true
        },
        meta: {
          requireAuth: true,
          roles: ['company']
        }
      }]
    },
    {
      path: 'realeaseDemand',
      component: () => import('@/components/ReleaseDemand'),
      meta: {
        requireAuth: true,
        roles: ['company']
      },
    }
  ],
  redirect: '/company/research/list/????�械'
}