<template>
  <v-menu
    :close-on-content-click="false"
    left
    bottom
    offset-y
    nudge-bottom="8"
    transition="scroll-y-transition"
  >
    <template #activator="{ on }">
      <v-btn class="body-1" v-on="on" text>{{ username }}</v-btn>
    </template>
    <v-card width="240">
      <v-list class="py-1">
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="font-weight-medium">{{
              username
            }}</v-list-item-title>
            <v-list-item-subtitle>{{ account }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item>
          <v-list-item-title class="body-2">
            <a class="grey--text text--darken-2" @click="handleToHistory()"
              >发布历史</a
            >
          </v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title class="body-2">
            <a class="grey--text text--darken-2" @click="handleToInfo()"
              >我的信息</a
            >
          </v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-list-item-title class="body-2">
            <router-link class="grey--text text--darken-2" to="/accountSetting"
              >账户设置</router-link
            >
          </v-list-item-title>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item @click="logout">
          <v-list-item-title class="grey--text body-2"
            >退出登录</v-list-item-title
          >
        </v-list-item>
      </v-list>
    </v-card>
  </v-menu>
</template>

<script>
export default {
  inject: ['reload'],
  name: 'UserMenu',
  data: () => ({
    role: '',
  }),
  computed: {
    username() {
      return sessionStorage.getItem('username')
    },
    account() {
      return sessionStorage.getItem('account')
    },
  },
  mounted() {},
  methods: {
    async logout() {
      this.$store.commit('logout')
      let res = await this.$http.get('/api/user/logout')
      if (res.data.code != 200) {
        this.$message.error('退出登录失败！')
        return false
      }
      if (this.$router.name !== 'Login') {
        this.$router.push({ name: 'Login' })
        this.$router.go(0)
      }
    },
    handleToInfo() {
      this.role = sessionStorage.getItem('role')
      this.$router.push('/' + this.role + '/personalInfo')
    },
    handleToHistory() {
      this.role = sessionStorage.getItem('role')
      this.$router.push('/' + this.role + '/history')
    },
  },
}
</script>

<style scoped></style>
