      >
      <v-spacer></v-spacer>
    </v-app-bar>
    <div class="main">
      <div>
        <v-img src="../../assets/img/13-4.jpg" style="opacity:0.85"> </v-img>
      </div>
      <v-container>
        <v-row>
          <v-col cols="12" sm="6">
            <v-card class="attention-logo white--text"
              >新闻列表
              <div class="more right">
                <router-link to="/newsList" class="subtitle-1"
                  >更多>></router-link
                >
              </div>
            </v-card>
            <div class="news">
              <ul>
                <li v-for="item in newslist.slice(0, 10)" :key="item.id">
                  <a class="link subtitle-1" @click="handleNew(item.id)">
                    <svg class="icon" aria-hidden="true">
                      <use
                        xlink:href="#icon-weimingmingwenjianjia_jiantou"
                      ></use></svg
                    >{{ item.title }}
                  </a>
                </li>
              </ul>
            </div>
          </v-col>
          <v-col cols="12" sm="6">
            <v-card class="attention-logo white--text"
              >公告列表
              <div class="more right">
                <router-link to="/noticeList" class="subtitle-1"
                  >更多>></router-link
                >
              </div>
            </v-card>
            <div class="attention">
              <ul>
                <li v-for="item in noticeList.slice(0, 10)" :key="item.id">
                  <a class="link subtitle-1" @click="handleNote(item.id)">
                    <svg class="icon" aria-hidden="true">
                      <use
                        xlink:href="#icon-weimingmingwenjianjia_jiantou"
                      ></use></svg
                    >{{ item.title }}
                  </a>
                </li>
              </ul>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'Home',
  data() {
    return {
      search: '', //绑定搜索的关键词
      newslist: [],
      noticeList: [], //公告列表
      screenWidth: document.body.clientWidth
    }
  },
  components: {
    AppFooter
  },
  methods: {
    async getNewsList() {
      let res = await this.$http.get('/api/home/news')
      this.newslist = res.data.data.list
    },
    async getNoticeList() {
      let res = await this.$http.get('/api/home/announces')
      this.noticeList = res.data.data.list
    },
    Login() {
      this.$router.push('/login')
    },
    handleNew(id) {
      this.$router.push('/newsList/detail/' + id)
    },
    handleNote(id) {
      this.$router.push('/noticeList/detail/' + id)
    }
  },
  mounted() {
    const that = this
    window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
      })()
    }
    if (this.screenWidth < 926) {
      this.isshow = false
    } else {
      this.isshow = true
    }
    this.getNewsList()
    this.getNoticeList()
  },
  watch: {
    screenWidth(val) {
      this.screenWidth = val
      if (this.screenWidth < 926) {
        this.isshow = false
      } else {
        this.isshow = true
      }
    }
  }
}
</script>

