import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import Login from '@/views/Login'
import Register from '@/views/Register'
import schoolRouter from './School'
import companyRouter from './Company'

Vue.use(VueRouter)

const routes = [
  schoolRouter,
  companyRouter,
  {
    path: '/',
    redirect: '/home'
  }, {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('@/views/About')
  },
  {
    path: '/newsList',
    name: 'NewsList',
    component: () => import('@/views/NewsList')
  },
  {
    path: '/newsList/detail/:id',
    component: () => import('@/views/New'),
    props: true
  },
  {
    path: '/noticeList',
    name: 'NoticeList',
    component: () => import('@/views/NoticeList')
  },
  {
    path: '/noticeList/detail/:id',
    component: () => import('@/views/Notice'),
    props: true
  },
  {
    path: '/message',
    name: 'Message',
    component: () => import('@/views/Message'),
    meta: {
      requireAuth: true,
      roles: ['university', 'company']
    }
  },
  {
    path: '/accountSetting',
    name: 'AccountSetting',
    component: () => import('@/views/AccountSetting'),
    meta: {
      //requireAuth: true,
      roles: ['university', 'company']
    }
  },
  {
    path: '/company/history',
    component: () => import('@/views/History'),
    meta: {
      requireAuth: true,
      roles: ['company']
    }
  },
  {
    path: '/company/history/detail/:id',
    component: () => import('@/views/CompanyEdit'),
    props: true,
    meta: {
      requireAuth: true,
      roles: ['company']
    }
  },
  {
    path: '/university/history',
    component: () => import('@/views/SHistory'),
    meta: {
      requireAuth: true,
      roles: ['university']
    }
  },
  {
    path: '/university/history/detail/:id',
    component: () => import('@/views/SchoolEdit'),
    props: true,
    meta: {
      requireAuth: true,
      roles: ['university']
    }
  },
  {
    path: '/company/personalInfo',
    component: () => import('@/views/CPersonalInfo'),
    meta: {
      requireAuth: true,
      roles: ['company']
    }
  },
  {
    path: '/university/personalInfo',
    component: () => import('@/views/PersonalInfo'),
    meta: {
      requireAuth: true,
      roles: ['university']
    }
  },
  {
    path: '/manager',
    name: 'manager',
    component: () => import('@/views/Manager')
  },
  {
    path: '*',
    name: 'NotFound',
    component: () => import('@/views/Error')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router