<template>
  <div class="back">
    <app-bar
      back-icon
      title="账号注册"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12 mb-12" min-height="400" max-width="800">
          <p class="text-center headline pt-5 pb-3">注册新账号</p>
          <v-divider></v-divider>
          <v-col cols="12" sm="6" class="mx-auto mt-5">
            <el-form
              label-position="left"
              label-width="80px"
              :model="form"
              :rules="rules"
              ref="formRef"
            >
              <el-form-item label="账号" prop="account">
                <el-input
                  v-model="form.account"
                  placeholder="6~18位字符，只能包含英文字母、数字、下划线"
                ></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input v-model="form.password" type="password"></el-input>
              </el-form-item>
              <el-form-item label="确认密码" prop="checkPass">
                <el-input v-model="form.checkPass" type="password"></el-input>
              </el-form-item>
              <el-form-item label="公司名称" prop="username">
                <el-input v-model="form.username"></el-input>
              </el-form-item>
              <el-form-item label="税号" prop="paragraph">
                <el-input v-model="form.paragraph"></el-input>
              </el-form-item>
              <el-form-item label="联系人" prop="contact">
                <el-input v-model="form.contact"></el-input>
              </el-form-item>
              <el-form-item label="联系电话" prop="tel">
                <el-input v-model="form.tel"></el-input>
              </el-form-item>
              <el-form-item label="联系邮箱" prop="mail">
                <el-input v-model="form.mail"></el-input>
              </el-form-item>
              <el-form-item label="公司地址" prop="address">
                <el-input v-model="form.address" type="textarea"></el-input>
              </el-form-item>
            </el-form>
            <div class="pt-5">
              <el-button type="primary" @click="register()">注册</el-button>
              <el-button type="info" @click="cancel()">取消</el-button>
            </div>
          </v-col>
        </v-card>
      </v-container>
    </v-content>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'Register',
  components: {
    AppFooter,
    AppBar
  },
  data() {
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/
      if (regEmail.test(value)) {
        // 合法邮箱
        return callback()
      }
      callback(new Error('请输入合法邮箱'))
    }
    // 自定义手机号规则
    var checkMobile = (rule, value, callback) => {
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        return callback()
      }
      // 返回一个错误提示
      callback(new Error('请输入合法的手机号码'))
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      checkPass: '',
      form: {
        username: '',
        role: 'company',
        account: '',
        password: '',
        paragraph: '',
        contact: '',
        mail: '',
        tel: '',
        address: ''
      },
      formRef: '',
      rules: {
        account: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        username: [
          { required: true, message: '请输入公司名称', trigger: 'blur' }
        ],
        mail: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: validatePass2, trigger: 'blur' }
        ],
        paragraph: [
          { required: true, message: '请输入公司税号', trigger: 'blur' }
        ],
        contact: [{ required: true, message: '请输入联系人', trigger: 'blur' }],
        tel: [
          { required: true, message: '请输入联系电话', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入公司地址', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    cancel() {
      this.form = ''
      this.$router.push('/login')
    },
    clearInput() {
      this.form = {
        username: '',
        account: '',
        password: '',
        paragraph: '',
        contact: '',
        mail: '',
        tel: '',
        address: ''
      }
    },
    async register() {
      let valid = this.$refs.formRef.validate()
      if (!valid) return
      let res = await this.$http.post('/api/user/register', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.clearInput()
        this.$message.success('用户注册成功！')
      } else {
        this.clearInput()
        this.$message.error('用户注册失败！')
      }
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
