import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: '',
    account: '',
    subject: ''
  },
  mutations: {
    handleUserName: (state, username) => {
      state.username = username
      sessionStorage.setItem('username', username)
    },
    handleAccount: (state, account) => {
      state.account = account
      sessionStorage.setItem('account', account)
    },
    handleSubject: (state, subject) => {
      state.subject = subject
      sessionStorage.setItem('subject', subject)
    },
    handleSelectSubject: (state, selectSubject) => {
      state.selectSubject = selectSubject
      sessionStorage.setItem('selectSubject', selectSubject)
    },
    logout(state) {
      sessionStorage.clear()
      state.username = ''
      state.account = ''
    }
  },
  actions: {},
  modules: {}
})