import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css'
import '@mdi/font/css/materialdesignicons.css'
import axios from 'axios'
import vuetify from './plugins/vuetify'

Vue.prototype.$http = axios
Vue.use(ElementUI)
Vue.config.productionTip = false

axios.interceptors.request.use(function (config) {
  return config;
}, function (error) {
  return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        case 150:
          sessionStorage.clear()
          router.push('/login')
          break
      }
    }
  }
)

router.beforeEach((to, from, next) => {
  let userToken = sessionStorage.getItem('userToken')
  let role = sessionStorage.getItem('role')
  if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
    if (userToken) { // 判断本地是否存在token
      if (to.meta.roles.length !== 0) {
        for (let i = 0; i < to.meta.roles.length; i++) {
          if (role === to.meta.roles[i]) {
            next()
            break
          } else if (i === to.meta.roles.length - 1) {
            next({
              path: '/Error'
            })
          }
        }
      } else {
        next()
      }
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    next()
  }
  /* 如果本地存在token,则不允许直接跳转到登录页面 */
  if (to.fullPath === '/login') {
    if (userToken) {
      if (role === 'university') {
        next({
          path: '/university/demand/list/船舶产业'
        })
      } else if (role === 'company') {
        next({
          path: '/company/research/list/船舶产业'
        })
      } else {
        next({
          path: from.fullPath
        })
      }
    } else {
      next()
    }
  }
})

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')