<template>
  <div class="back">
    <v-app-bar app elevation="2" color="#333744" dark>
      <v-toolbar-title style="cursor: pointer;">
        <span>靖江市科技镇长团</span>
      </v-toolbar-title>
      <router-link class="ml-5 subtitle-1 white--text" to="/">首页</router-link>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container fill-height>
        <v-card flat class="ma-auto" elevation="4" min-width="350">
          <v-card-title>登录</v-card-title>
          <v-card-text class="mx-auto">
            <v-form v-model="valid" ref="form">
              <v-text-field
                v-model="form.username"
                prepend-icon="mdi-account"
                label="账号"
                placeholder="请输入账号"
                name="login"
                type="text"
                :rules="[(v) => !!v || '请输入账号或者手机号']"
              ></v-text-field>
              <v-text-field
                v-model="form.password"
                prepend-icon="mdi-lock"
                type="password"
                label="密码"
                placeholder="密码"
                name="password"
                :rules="[(v) => !!v || '请输入密码']"
              ></v-text-field>
              <div class="error--text ml-8">{{ msg }}</div>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="primary" @click="register()">企业注册</v-btn>
            <v-spacer></v-spacer>
            <v-btn
              text
              color="primary"
              :loading="isLoading"
              @click="login('formLogin')"
              >登录</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppFooter from '@/components/App/AppFooter'
import qs from 'qs'

export default {
  name: 'Login',
  components: { AppFooter },
  data() {
    return {
      form: {
        username: '',
        password: '',
      },
      isLoading: false,
      msg: '',
      valid: true,
    }
  },
  methods: {
    async login() {
      let valid = this.$refs.form.validate()
      if (!valid) return
      this.isLoading = true
      let res = await this.$http.post(
        '/api/user/login',
        qs.stringify(this.form),
        {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        }
      )
      if (res.status !== 200) {
        this.isLoading = false
        this.$message.error('登录失败')
        return false
      }
      if (res.data.code != 200) {
        this.isLoading = false
        this.msg = res.data.msg
        return false
      }
      //存入全局数据
      this.$store.commit('handleUserName', res.data.data.userInfo.username)
      this.$store.commit('handleAccount', res.data.data.userInfo.account)
      sessionStorage.setItem('userToken', res.data.data.token)
      sessionStorage.setItem('role', res.data.data.userInfo.role)
      this.clearInput()
      this.isLoading = false
      this.goNameRouter(res.data.data.userInfo.role)
    },
    clearInput() {
      this.form = {
        loginId: '',
        password: '',
      }
    },
    goNameRouter(role) {
      if (role === 'manager') {
        window.location.href =
          'http://212.64.87.193/' + role + '/manager/userlist'
      }
      this.$router.push(role + '/')
    },
    register() {
      this.clearInput()
      this.$router.push('/register')
    },
  },
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
