<template>
  <v-app-bar app elevation="2" color="#333744" dark>
    <v-app-bar-nav-icon @click="goBack()" v-if="backIcon">
      <v-icon v-html="'mdi-arrow-left'"></v-icon>
    </v-app-bar-nav-icon>
    <v-toolbar-title>
      <span class="pr-8">{{ title || appName }}</span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn
      color="light-blue darken-1"
      v-show="!hideDemand"
      @click="handleToRelease()"
      class="mr-5"
    >
      <v-icon>mdi-plus</v-icon>
      {{ releaseText }}
    </v-btn>
    <full-screen-btn class="hidden-sm-and-down"></full-screen-btn>
    <v-tooltip bottom>
      <template #activator="{on}">
        <v-btn
          v-show="!hideMessage"
          @click="goNameRouter('/message')"
          icon
          v-on="on"
        >
          <v-icon>mdi-bell</v-icon>
        </v-btn>
      </template>
      <span>消息中心</span>
    </v-tooltip>
    <v-toolbar-items>
      <user-menu v-if="!hideUserMenu"></user-menu>
    </v-toolbar-items>
  </v-app-bar>
</template>

<script>
import FullScreenBtn from './FullScreenBtn'
import UserMenu from './UserMenu'

export default {
  name: 'AppBar',
  components: { UserMenu, FullScreenBtn },
  props: {
    title: { type: String },
    subTitle: { type: String },
    backIcon: { type: Boolean, default: false },
    hideMessage: { type: Boolean, default: false },
    hideUserMenu: { type: Boolean, default: false },
    hideDemand: { type: Boolean, default: false },
    newMessage: { type: Boolean, default: false }
  },
  computed: {
    dark() {
      return this.$vuetify.theme.dark
    },
    appName() {
      return '靖江市科技镇长团'
    },
    noIcon() {
      return !this.backIcon
    }
  },
  data() {
    return {
      crole: '',
      releaseText: ''
    }
  },
  methods: {
    goBack() {
      if (this.crole === 'company') {
        this.$router.push('/company')
      } else if (this.crole === 'university') {
        this.$router.push('/university')
      } else {
        this.$router.back()
      }
    },
    goNameRouter(path) {
      this.$router.push(path)
    },
    handleToRelease() {
      if (this.crole === 'company') {
        this.$router.push('/company/realeaseDemand')
      } else if (this.crole === 'university') {
        this.$router.push('/university/realeaseScience')
      }
    },
    handleToText() {
      this.crole = sessionStorage.getItem('role')
      if (this.crole === 'company') {
        this.releaseText = '发布需求'
      } else if (this.crole === 'university') {
        this.releaseText = '发布成果'
      }
    }
  },
  mounted() {
    this.handleToText()
  }
}
</script>

<style scoped></style>
